import { Suspense, ReactElement, useContext, useRef, useEffect, useState } from "react";
import { Canvas } from "@react-three/fiber";
import { Preload, MapControls } from "@react-three/drei";
import { Object3D, InstancedMesh } from "three";
import Loader from "../../components/loader";
import Plane from "../../components/plane";
import Lights from "../../components/lights";
import { StateContext } from "../../context/providers/State";
import { JSONObject } from "../../context/providers/initialState";
import { transpileModule } from "typescript";

const colorMap:any = {
  ES: '#ff0000',
  IT: '#00ff00',
  FR: '#0000ff',
  CH: '#f0f0f0',
  PT: '#0f0f0f',
}

function Instances({ temp, list}: { temp: Object3D; list: Array<JSONObject> }) {
  const ref = useRef<InstancedMesh>(null!);
  const [rendered, setRendered] = useState(false)
  useEffect(() => {
    // Set positions
    if(ref.current && !rendered){
      for (let i = 0; i < list.length; i++) {
        const height = Number(list[i].population) * 1e-6 < .05
          ? .05
          : Number(list[i].population) * 1e-6 < 10
            ? Number(list[i].population) * 1e-6
            : 10
        temp.position.set(-Number(list[i].coordinates.lat), 0, -Number(list[i].coordinates.lon))
        temp.scale.set(0.05, height, 0.05)
        temp.updateMatrix()
        ref.current.setMatrixAt(i, temp.matrix)
      }
      // Update the instance
      ref.current.instanceMatrix.needsUpdate = true
      setRendered(true)
    }
  }, [list, temp, rendered])
  return (
    <instancedMesh ref={ref} args={[undefined, undefined, list.length]}>
      <boxGeometry />
      <meshPhongMaterial color={'#ff0000'}/>
    </instancedMesh>
  )
  
          
}

